<template>
  <div class="ml-2 mb-2 border p-2">
    <b-row>
      <b-col lg="3" v-for="(v,k,i) in ds" :key="i">
        <input disabled @click="onclick()" type="checkbox" id="checkbox" v-model="ds[k]">
        {{ k }}
      </b-col>
    </b-row>
  </div>
</template>
<script>
import fetchClient from '@/fetchClient'
import TumLum from '@/TumLum'
export default {
  props:["RoleId"],
  components: {
  },
  data() {
    return {
      ds:[]
    }
  },
  mounted() {
    this.GetPermissionByRoleId(this.RoleId);
  },
  methods: {
    onclick(){
      alert("hello")
    },
    async GetPermissionByRoleId(RoleId) {
      return await fetchClient.get(`/api/Permission/GetPermissionByRoleId?RoleId=${RoleId}`)
        .then(res => {
          //alert(JSON.stringify(res));
          //{"data":{"them.hs":false,"xoa.hs":false,"sua.hs":false,"xem.hs":false,"them.ban":false,"them.mo
          this.ds=res.data;
      }).catch(err => {
        TumLum.XuLyErr(err);
      })
    }
  }
}
</script>
<style>
.large-font {
  font-size: 11px;
}
</style>
  