<template>
    <div class="home">
        <h4 class="border-bottom p-1">Claim User</h4>
        <ul>
            <li v-for="x in ds2" :key="x">
                {{ x.ClaimValue }}
                <b-badge variant="primary" v-for="y in x.List" :key="y">{{y}}</b-badge>
            </li>
        </ul>
    </div>
</template>
<script>
import fetchClient from '@/fetchClient'
import TumLum from '@/TumLum'
export default {
    components: {
    },
    data() {
        return {
            ds: [],
            users: [],
            ds2: []
        }
    },
    mounted() {
        this.ClaimUser_GetAll();
    },
    methods: {

        async ClaimUser_GetAll() {
            return await fetchClient.get(`/api/NguoiDung5/ClaimUser_GetAll`).then(res => {
                //alert(JSON.stringify(res));
                this.ds = res.data;
                this.GetUserCombo();
            }).catch(err => {
                TumLum.XuLyErr(err);
            })
        },

        async GetUserCombo() {
            return await fetchClient.get(`/api/NguoiDung5/GetComboNguoiDung`)
                .then(res => {
                    //this.comboUser = res.data;
                    this.users = res.data;
                    //{"data":[{"Id":1,"ClaimValue":"them.hs","List":[]},{"Id":2,"ClaimValue":"xoa.hs","List":[]},{"Id":3,"Claim   
                    this.ds.forEach(x => {
                        let ClaimValue = x.ClaimValue;
                        let List = x.List;
                        let List2 = [];
                        List.forEach(y => {
                            let index=this.users.findIndex(m=>m.id==y);
                            if(index >=0 ) List2.push(this.users[index].label);
                        })
                        this.ds2.push({
                            ClaimValue: ClaimValue,
                            List: List2
                        })
                    })


                })
                .catch((err) => {
                    TumLum.XuLyErr(err);
                });
        },



    }
}
</script>
  