export default "<template>\r\n  <div class=\"home\">\r\n    <raw-loader :mytxt=\"require('!raw-loader!./Permission.vue').default\"></raw-loader>\r\n    <h1>Permission</h1>\r\n    <b-row>\r\n      <b-col md=\"12\">\r\n      </b-col>\r\n      <b-col md=\"5\">\r\n        <b-img src=\"https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcQFOXi_UoyjzyFmWtvs9YWYkpG0WThjeziAhg&usqp=CAU\"\r\n          fluid alt=\"Fluid image\"></b-img>\r\n        <div class=\"border\">\r\n          <PermissionClaimUserCom></PermissionClaimUserCom>\r\n        </div>  \r\n      </b-col>\r\n      <b-col md=\"7\">\r\n\r\n        <div>\r\n          <div v-for=\"x in roles\" :key=\"x.Id\">\r\n            <h4>{{ x.Role }}</h4>\r\n            <!-- { \"Id\": 1, \"Role\": \"Admin\", \"CapDo\": 9 } -->\r\n            <PermissionCom :RoleId=\"x.Id\"></PermissionCom>\r\n          </div>\r\n        </div>\r\n\r\n\r\n      </b-col>\r\n    </b-row>\r\n  </div>\r\n</template>\r\n<script>\r\nimport fetchClient from '@/fetchClient'\r\nimport TumLum from '@/TumLum'\r\nimport PermissionCom from \"@/views/NguoiDung2/PermissionCom.vue\"\r\nimport PermissionClaimUserCom from \"@/views/NguoiDung2/PermissionClaimUserCom.vue\"\r\nexport default {\r\n  components: {\r\n    PermissionCom,\r\n    PermissionClaimUserCom\r\n  },\r\n  data() {\r\n    return {\r\n      res: [],\r\n      roleClaims: [],\r\n      userRoles: [],\r\n\r\n\r\n\r\n      roles: [],\r\n    }\r\n  },\r\n  mounted() {\r\n    this.GetAllRole();\r\n  },\r\n  methods: {\r\n    getRoleClaimByRole(Role) {\r\n      let ar = [];\r\n      this.roleClaims.forEach(x => {\r\n        if (x.Role == Role) {\r\n          ar.push(x);\r\n        }\r\n      })\r\n      return ar;\r\n    },\r\n    async GetPermission() {\r\n      return await fetchClient.get(`/api/NguoiDung5/GetPermission`).then(res => {\r\n        this.res = res;\r\n        this.roleClaims = res.data.roleClaims;\r\n        this.userRoles = res.data.userRoles;\r\n      }).catch(err => {\r\n        TumLum.XuLyErr(err);\r\n      })\r\n    },\r\n    async GetAllRole() {\r\n      return await fetchClient.get(`/api/Permission/GetAllRole`).then(res => {\r\n        //alert(JSON.stringify(res));\r\n        //{\"data\":[{\"Id\":1,\"Role\":\"Admin\",\"CapDo\":9},{\"Id\":2,\"Role\":\"User\",\"CapDo\":1},{\"Id\":3,\"Role\":\"NhanVien\",\"CapDo\":2},{\"Id\":4,\" \r\n        this.roles = res.data;\r\n      }).catch(err => {\r\n        TumLum.XuLyErr(err);\r\n      })\r\n    }\r\n  }\r\n}\r\n</script>\r\n<style>\r\n.large-font {\r\n  font-size: 11px;\r\n}\r\n</style>\r\n  ";